.projects p{
    width: 300px;
    margin-top: 20px;
}
.projects i {
    margin: 2rem;
    color: rgb(248, 248, 248);
}
.projects .card {
    width: 95%;
   
    border: 0;
    background-image: linear-gradient(135deg,#17ECFC,rgb(94, 34, 235));
    position: relative;
    opacity: 0;
    border-radius: 20px;
    animation: cardani 2s .5s forwards;
    margin-top: 80px;
    height: 400px;


}
.projects .card h5 {
    
    font-size :  1.5rem;
    font-weight: bold;
    font-style: italic;
    color: white;
    margin-bottom: 1.2rem;
}
.projects .card p {
    
    font-size :  .8rem;
    font-style: italic;
    color: rgba(255, 255, 255, 0.589);
    margin-bottom: 1.2rem;
    width: 250px;
}
.projects .card a{ 
    background-image: linear-gradient(rgb(255, 213, 0),#FFE877);
    border:0;
    border-radius: 20px; 
    color: black;
    font-size: .8rem;
    margin-bottom: 1.2rem;
}
@keyframes cardani{
    0% {top: 5rem; opacity: 0;}
    100%{top: 0; opacity: 1;}
}
